body {
  background-color: #cbeeaa;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

a {
  color: #333;
  text-decoration: none;
}

p {
  margin: 0;
}

.search-form {
  width: 500px;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.search-form input[type="text"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline-color: #4d944d;
  padding: 10px;
}

.container {
  background-color: #4d944d;
  justify-content: center;
  align-items: center;
  display: flex;
}

.photo-list {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  width: 300px;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.img-wrapper {
  width: 300px;
  height: 200px;
}

.info {
  padding: 15px;
}

.movie-info h2 {
  color: #333;
  margin: 0;
  font-size: 1.8em;
}

.movie-info p {
  color: #777;
  margin: 5px 0;
  font-size: 1.2em;
}

.info-item {
  justify-content: space-between;
  display: flex;
}

.count {
  color: #6495ed;
  text-align: right;
}

.button-container {
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  display: flex;
}

.btn-search {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #1da74b;
  border: none;
  border-radius: 5px;
  margin-left: 20px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: all .5s;
  display: inline-block;
  box-shadow: 0 5px 15px #0bf659;
}

.btn-search:hover {
  transform: translate(0, -3px);
  box-shadow: 0 20px 40px #0bf659;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #1da74b;
  border: none;
  border-radius: 5px;
  margin: 0 auto 20px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: all .5s;
  display: inline-block;
  box-shadow: 0 5px 15px #0bf659;
}

.load-more:hover {
  transform: translate(0, -3px);
  box-shadow: 0 20px 40px #0bf659;
}

.load-more-hidden {
  display: none;
}

