body{
    background-color: rgb(203, 238, 170);
}

ul,
ol {
    list-style-type: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

a {
text-decoration: none;
color: #333;
}

p{
    margin: 0;
}

.search-form{
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 500px;
        
}

.search-form input[type='text'] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
    outline-color: rgb(77, 148, 77);
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(77, 148, 77);
}

.photo-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    
    margin: 0;
}

/* .load-more:hover {
    background-color: #0056b3;
    transform: scale(1.05);
} */

.photo-card {
    position: relative;
    width: 300px;
    height: 300px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.photo-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.photo-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.img-wrapper{
width: 300px;
height: 200px;
}

.info {
    padding: 15px;
}

.movie-info h2 {
    margin: 0;
    font-size: 1.8em;
    color: #333;
}

.movie-info p {
    margin: 5px 0;
    font-size: 1.2em;
    color: #777;
}

.info-item{
    display: flex;
    justify-content:space-between ;
}

.count{
    color: cornflowerblue;
    text-align: right;
}

.button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
}
.btn-search{
    display: inline-block;
        padding: 10px 20px;
        margin-left: 20px;
        font-size: 1.2em;
        font-weight: bold;
        text-align: center;
        text-transform: uppercase;
        border: none;
        border-radius: 5px;
        color: #fff;
        cursor: pointer;
        background-color: rgb(29, 167, 75);
        box-shadow: 0 5px 15px 0 rgb(11, 246, 89, 1);
        transition: 0.5s;
}

.btn-search:hover{
    transform: translate(0, -3px);
     box-shadow: 0 20px 40px 0 rgba(11, 246, 89, 1);
}

.load-more {
    margin: 0 auto;
    margin-bottom: 20px;
       
    display: inline-block;
    padding: 10px 20px;
    font-size: 1.2em;
     font-weight: bold;
    text-align: center;
     text-transform: uppercase;
     border: none;
     border-radius: 5px;
     color: #fff;
    cursor: pointer;
    background-color: rgb(29, 167, 75);
    box-shadow: 0 5px 15px 0 rgb(11, 246, 89, 1);
    transition: 0.5s;
}

.load-more:hover {
    transform: translate(0, -3px);
    box-shadow: 0 20px 40px 0 rgba(11, 246, 89, 1);
}

.load-more-hidden {
    display: none;
}